<template>
  <div class="org">
    <div class="table-data">
      <!-- 				操作 -->

      <iot-table
        :tableHeader="tableHeader"
        :tableData="tableData"
        :pagination="pagination"
        :loading="loading"
        @reloadData="initData"
      >
        <div slot="col-type" slot-scope="scope"><p>{{scope.row.type === 'HQ' ? '总代' : '代理商'}}</p></div>
        <div slot="col-province" slot-scope="scope"><p>{{scope.row.province || ''}}{{scope.row.city || ''}}{{scope.row.county || ''}}{{scope.row.village || ''}}</p></div>
      </iot-table>
    </div>
  </div>
</template>

<script>
import { getAuthlist } from '@/api/system'
import iotTable from '@/components/iotTable'


export default {
  name: 'Permission',
  components: {
    iotTable
  },
  data() {
    return {
      loading: false,
      queryFilter: {
        name: '',
        code: '',
        type: ''
      },
      type: [
        {
          code: '',
          name: '全部'
        },
        {
          code: 'HQ',
          name: '总代'
        },
        {
          code: 'CH',
          name: '代理商'
        }
      ],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableData: [],
      tableHeader: [
        { prop: 'name', label: '代理商名称' },
        { prop: 'code', label: '编码' },
        { prop: 'type', label: '组织类型', type: 'slot' },
        { prop: 'members_count', label: '人员数' },
        { prop: 'province', label: '所在地区', type: 'slot' },
        { prop: 'apply_reason', label: '申请原因' },
        { prop: 'create_user', label: '创建人' },
        { prop: 'memo', label: '备注' }
      ]
    }
  },
  mounted() {
    this.initData()
  },
  methods: {
    async initData() {
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        name: this.queryFilter.name ? this.queryFilter.name : undefined,
        code: this.queryFilter.code ? this.queryFilter.code : undefined,
        type: this.queryFilter.type ? this.queryFilter.type : undefined,
        status: 1
      }
      this.loading = true
      const { data } = await getAuthlist(params)
      this.tableData = data.items
      this.pagination = {
        total: data.page_info.total,
        page: data.page_info.page,
        per_page: data.page_info.per_page
      }
      this.loading = false

    }
  }
}
</script>

