<template>
  <div class="org">
    <div class="data1">
      <div class="selects">
        <div class="select-titles">
          <el-row :gutter="24">
            <el-col :span="6"><div class="common-item">公司名称</div></el-col>
            <el-col :span="6"><div class="common-item">公司编码</div></el-col>
            <el-col :span="6"><div class="common-item">组织类型</div></el-col>
            <el-col :span="6"><div class="common-item"></div></el-col>
          </el-row>
        </div>
        <div class="select-content">
          <el-row :gutter="24">
            <el-col :span="6">
              <div class="common-item selsct-common">
                <el-input v-model="queryFilter.name" placeholder="请输入公司名称"></el-input>
              </div>
            </el-col>
            <el-col :span="6">
              <div class="common-item selsct-common">
                <el-input v-model="queryFilter.code" placeholder="请输入公司编码"></el-input>
              </div>
            </el-col>
            <el-col :span="6">
              <div class="common-item selsct-common">
                <el-select v-model="queryFilter.type" placeholder="请选择事件类型">
                  <el-option
                    v-for="item in type"
                    :key="item.code"
                    :label="item.name"
                    :value="item.code">
                  </el-option>
                </el-select>
              </div>
            </el-col>
            <el-col :span="6">
              <el-button class="green" type="primary" @click="initData">查询</el-button>
            </el-col>
          </el-row>
        </div>
      </div>
      <div class="table-data">
        <iot-table
          :tableHeader="tableHeader"
          :tableData="tableData"
          :pagination="pagination"
          :loading="loading"
          @reloadData="initData"
        >
          <div slot="col-type" slot-scope="scope"><p>{{scope.row.type === 'HQ' ? '总代' : '代理商'}}</p></div>
          <div slot="col-province" slot-scope="scope"><p>{{scope.row.province || ''}}{{scope.row.city || ''}}{{scope.row.county || ''}}{{scope.row.village || ''}}</p></div>
        </iot-table>
      </div>
    </div>
  </div>
</template>

<script>
import { getAuthlist } from '@/api/system'
import iotTable from '@/components/iotTable'

export default {
  name: 'Permission',
  components: {
    iotTable
  },
  data() {
    return {
      loading: false,
      queryFilter: {
        name: '',
        code: '',
        type: ''
      },
      type: [
        {
          code: '',
          name: '全部'
        },
        {
          code: 'HQ',
          name: '总代'
        },
        {
          code: 'CH',
          name: '代理商'
        }
      ],
      pagination: {
        page: 1,
        per_page: 10,
        total: 0
      },
      tableData: [],
      tableHeader: [
        { prop: 'name', label: '代理商名称' },
        { prop: 'code', label: '编码' },
        { prop: 'type', label: '组织类型', type: 'slot' },
        { prop: 'members_count', label: '人员数' },
        { prop: 'province', label: '所在地区', type: 'slot' }
      ]
    }
  },
  mounted() {
    this.initData()
  },
  methods: {
    async initData() {
      this.loading = true
      const params = {
        page: this.pagination.page,
        per_page: this.pagination.per_page,
        name: this.queryFilter.name ? this.queryFilter.name : undefined,
        code: this.queryFilter.code ? this.queryFilter.code : undefined,
        type: this.queryFilter.type ? this.queryFilter.type : undefined,
        status: 0
      }
      const { data } = await getAuthlist(params)
      this.tableData = data.items
      this.pagination = {
        total: data.page_info.total,
        page: data.page_info.page,
        per_page: data.page_info.per_page
      }
      this.loading = false
    }
  }
}
</script>

